import type { Delimiter } from '@ali/4ever-utils';
import createMarkButton, { createMarkMobileButton } from './components/createMarkButton';
import { getMarkdownFromDelimiter } from './utils';

export default function createToolbar(type, hotKey, Button, Icon, delimiter?: Delimiter | Delimiter[]) {
  return function toolbar(pluginConfig) {
    const pluginLocaleConfig = pluginConfig.locale || {};
    const markdown = delimiter ? getMarkdownFromDelimiter(delimiter, pluginLocaleConfig) : '';
    return {
      buttons: {
        default: createMarkButton(type, hotKey, Button, markdown),
        mobile: createMarkMobileButton(type, Icon),
      },
      locale: pluginLocaleConfig,
    };
  };
}
